export default {
	data() {
		return {
			shopname:'',
			address:'',
			h_name:'',
			price:'',
			times:'',
			door_photos:'',
			hid:"",
			uid:"",
			dingId:"",
			data:""
		}
	},
	onLoad(e) {
		const that = this
		var id = e.id
		this.hid =id;
		this.makeIndex(id)
	    const message = uni.getStorageSync("token")
	    that.uid= message.uid
	    that.data = new Date()
	},
	methods: {
		async makeIndex(id){
			const that = this
			const res = await this.$myRequest({
				method:"POST",
				url:"/makeOrder?hid=" + id
			})
			that.shopname = res.data.data.shopname
			that.address = res.data.data.address
			that.h_name = res.data.data.h_name
			that.price = res.data.data.price
			that.times = res.data.data.times
			that.door_photos = res.data.data.door_photos
			that.dingId = res.data.data.id
		},
		zhifu(info){			
			uni.requestPayment({
				provider:"wxpay",
				timeStamp:info.timeStamp,
				nonceStr:info.nonceStr,
				package:info.package,
				signType:info.signType,
				paySign:info.paySign,
				orderInfo:info.out_trade_no,
				success:function(res){
					uni.navigateTo({
						url:"../payment_success/payment_success"
					})
						
				},
				fail:function(err){
					uni.navigateTo({
						url:"../payment_failure1/payment_failure1?id="+ info.out_trade_no
					})
											
				}
			})
		},
		async makePay(){
			const that = this
			const uid = that.uid
			const hid = that.hid
			const res = await this.$myRequest({
				method:"POST",
				url:"/topay",
				data:{
					uid:uid,
					hid:hid
				}
			})
			const info = res.data
			that.zhifu(info)
		}
	}
}
